
import Search from "@/components/search";
import XInput from 'vux/src/components/x-input/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'

import { JDGetIncomingList } from "@/api/getData";
import moment from 'moment'
export default {
data() {
return {
result: [],
showToast: false,
startDate: moment(new Date()).format('YYYY-MM-DD'),
endDate: moment().add('days', 1).format('YYYY-MM-DD'),
Supplier:'',
years:['今日','近7天','近30天'],
year1:'今日'
};
},
directives: {
TransferDom
},
components: {
XInput,
Tabbar,
TabbarItem,
XButton,
XDialog,
Datetime,
Group,
PopupRadio
},
mounted() {
this.getIncomingList();
},
methods: {
async getIncomingList() {
let data = await JDGetIncomingList({
StartDate: this.startDate,
EndDate: this.endDate,
Supplier: this.Supplier,
Type: "2"
});
console.log(eval(data));

this.result = eval(data);



},
toInfo(index) {
this.$router.push({
path: "/auditForm/inspectionWatch",
query: { FInterID: this.result[index].FInterID }
});
},
doShowToast () {
this.$vux.toast.show({
text: 'toast'
})
},
log (str1, str2 = '') {
console.log(str1, str2)
},
onConfirm (val) {
console.log('on-confirm arg', val)
console.log('current value', val)
this.getIncomingList()
},

















changeStart (value) {
this.startDate=value
console.log('changeStart', value)
},
changeEnd (value) {
this.endDate=value
console.log('changeEnd', value)
},
search(){
console.info(1)
this.getIncomingList()
},
change (value) {
console.log('new Value', value)
if(value=="今日"){
this.startDate=moment(new Date().getTime()).format('YYYY-MM-DD');
this.endDate=moment().add('days', 1).format('YYYY-MM-DD');
}
if(value=="近7天"){
this.startDate=moment(new Date().getTime() - 3600 * 24 * 7000).format('YYYY-MM-DD');
this.endDate=moment().add('days', 1).format('YYYY-MM-DD');
}
if(value=="近30天"){
this.startDate=moment(new Date().getTime() - 3600 * 24 * 30000).format('YYYY-MM-DD');
this.endDate=moment().add('days', 1).format('YYYY-MM-DD');
}
this.getIncomingList()
}
}
};



